import React, { useEffect, useState } from 'react';
import { IonContent, IonHeader, IonPage, IonTitle, IonToolbar, IonCard, IonCardHeader, IonCardSubtitle, IonCardTitle, IonCardContent, IonItem, IonIcon, IonLabel, IonButton, IonFab, IonFabButton, IonModal, IonInput, useIonViewDidEnter, useIonViewDidLeave, IonText, IonRow, IonCol } from '@ionic/react';
import Card from '../components/Card';
import './Tab1.css';
import { useHistory, withRouter } from 'react-router-dom';

import { pulse, videocam, closeOutline } from 'ionicons/icons'

var timeout : NodeJS.Timeout;
// var apiBase = "https://reelerapi.azurewebsites.net";
var apiBase = window["config"].apiBase;

const EventsListPage: React.FC<any> = () => {
  let history = useHistory();

  const [text, setText] = useState<string>();
  const [thumbUrl, setThumbUrl] = useState<string>();

  const [events, setEvents] = useState<any[]>([]);


  const getEvents = () => {

    fetch(apiBase + "/event")
      .then(x => x.json())
      .then(x => setEvents(x));
  }

 

  const addEvent = (event: any, thumbUrl: any) => {
    if(!event) return;
    fetch(apiBase + "/event", {
      method: "POST",
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify({name: event, DefaultThumbnail: thumbUrl})
    })
    .then(x => x.json())
    .then(x => setEvents(x));
    setText("");
  }

  const deleteEvent = (event: any) => {
    fetch(apiBase + "/event/" + event, {
      method: "DELETE",
    })
    .then(x => x.json())
    .then(x => setEvents(x));
  }

  useIonViewDidEnter(() => { 
    getEvents();
    clearInterval(timeout);
    timeout = setInterval(getEvents, 5000);
  });

  useIonViewDidLeave(() => {
    clearInterval(timeout);
  });

  return (
    <IonPage>
      {/* <IonHeader>
        <IonToolbar>
          <IonTitle>Live Streaming</IonTitle>
        </IonToolbar>
      </IonHeader> */}
      <IonContent className="bg-class">
        <IonText><h1  className="reeler-title">Reeler</h1></IonText>
        <p className="reeler-subtitle">{events.length} events</p>
        <IonInput style={{color: "white"}} value={text} placeholder="Enter Event name" onIonChange={e => setText(e.detail.value!)}></IonInput>
        <IonInput style={{color: "white"}} value={thumbUrl} placeholder="Enter Event Thumb Url" onIonChange={e => setThumbUrl(e.detail.value!)}></IonInput>
        <IonButton color="primary" expand="block" onClick={x => addEvent(text, thumbUrl)}>Start Event <IonIcon style={{ marginLeft: "10px" }} icon={videocam} /></IonButton>
        <div style={{}}>
          <IonRow>
          {
            events.map(x => {
              return (
                <IonCol size="6">
                   <IonCard className="ion-no-margin" onClick={e => {
                      e.preventDefault();
                      history.push(`/tab1/` + x.name);
                    }}>
                  <img src={x.defaultThumbnail} />
                  <button className="button button-light" onClick={e =>
                    {
                      e.stopPropagation(); 
                      deleteEvent(x.name);
                    }}>
                      <IonIcon icon={closeOutline} />
                    </button>
                  <IonCardHeader>
                    {/* <IonCardSubtitle>Live Now <IonIcon icon={pulse} /></IonCardSubtitle> */}
                    <IonCardTitle>{x.name}</IonCardTitle>
                  </IonCardHeader>
                </IonCard>
                </IonCol>
               );
            })
          }
          </IonRow>
          
        </div>

      </IonContent>
    </IonPage>
  );
};

export default withRouter(EventsListPage);
